<template>
  <div>
    <!--<div class="box" v-if="iSshow">账号管理:-->
    <!--</div>-->
    <div>
      管理员(A) :admin<br/>
      管理员(B)：admin1<br/>
      管理员(C)：admin2<br/>
    </div>
    <el-form  class="login_box" label-position="right" label-width="60px" :model="loginfrom">
      <el-form-item label="用户名">
        <el-input v-model="loginfrom.username"></el-input>
      </el-form-item>
      <el-form-item label="密码">
        <el-input type="password" v-model="loginfrom.pew"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submituUserInfo(loginfrom)">登录</el-button>
      </el-form-item>
    </el-form>
  </div>

</template>

<script>
export default {
  name: 'login',
  data () {
    return {
     loginfrom:{
      username:'',
      pew:'123456'
     },
      iSshow:true
    }
  },
  created(){
    let self = this;
    console.log(this);
    console.log(self)
    setTimeout(function(){
      alert('欢迎')
      self.iSshow=false;
    },1000);
  },
  methods:{
     submituUserInfo(data){
        console.log(data);
         this.$store.dispatch('Logins',data).then(res => {
           console.log(res)
           console.log( this.$store.state.login.username)
           if(sessionStorage.getItem('access_token')){
             this.$router.push({ path: '/list' ,query:{username:res.username}});
           }else{
             alert('access_token不存在');
           }
        }).catch(() => {

        })
     }
  }
}
</script>

<style scoped>
.login_box{
    width: 280px;
    position: fixed;
    left: 39%;
    top: 200px;
    border: 1px solid skyblue;
    border-radius: 15px;
    padding: 38px 60px 5px 25px;
    text-align: center;
}
  .box{
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 1;
    margin-bottom: 100px;
  }
</style>



