import Vue from 'vue'
import App from './App'
import router from './router'
import { powerRouter } from './router';
import store from './store';
import 'normalize.css'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import axios from 'axios'
import VueQriously from 'vue-qriously'
import 'mint-ui/lib/button/style.css';
import VueScroller from 'vue-scroller'
Vue.use(VueScroller)
Vue.use(VueQriously)
Vue.prototype.axios = axios;
Vue.use(ElementUI)
Vue.config.productionTip = false;
console.log('store',store);
router.beforeEach((to, from, next) => {
    if(store.getters.role){ //判断role 是否存在
      if(store.getters.newrouter.length !== 0){
        next();
      }else{
        let newrouter;
        if (store.getters.role == 'A') {  //判断权限
          console.log(store.getters.role);
          newrouter = powerRouter
        }
        else {
          let newchildren = powerRouter[0].children.filter(route => {
            if(route.meta){
              if(route.meta.role == store.getters.role){
                return true
              }
              return false
            }else{
              return true
            }
          });
          newrouter = powerRouter;
          newrouter[0].children = newchildren
        }
        router.addRoutes(newrouter) //添加动态路由
        store.dispatch('Roles',newrouter).then(res => {
          next({ ...to });
          //路由加载完以后判断页面token。
          if (!sessionStorage.getItem('access_token')) {
            alert('未登录，请跳转登录页进行登录')
            next('/login')
          }else{
            next();
          }
        }).catch(() => {

        })
      }
    }else{
    if(['/'].indexOf(to.path)===-1){
      console.log(['/login'].indexOf(to.path) !== -1);
      console.log(sessionStorage.getItem('access_token'));
      if (['/login'].indexOf(to.path) !== -1) {
          next()
      } else {
        alert('未登录，请跳转登录页进行登录');
        next('/login')
      }
    }else{
      next()
    }
    }
});
// http request 拦截器
axios.interceptors.request.use(
  config => {
    if (sessionStorage.getItem('access_token')) {  // 判断是否存在token，如果存在的话，则每个http header都加上token
      config.headers.Authorization = `token ${sessionStorage.getItem('access_token')}`;
    }
    return config;
  },
  err => {
    return Promise.reject(err);
  });

// http response 拦截器
axios.interceptors.response.use(
  response => {
    return response;
  },
  error => {
    if (error.response) {
      console.log(error.response);
     function to(){
       // 返回 401 清除token信息并跳转到登录页面
       sessionStorage.removeItem('USERNAME');
       sessionStorage.removeItem('access_token');
       sessionStorage.removeItem('ROLE');
       alert('返回错误 清除token信息并跳转到登录页面')
       router.replace({
         path: '/login',
       });
     }
      switch (error.response.status) {
        case 404:
          to();
          break;
        case 405:
          to();
          break;
      }
    }
    return Promise.reject(error.response)   // 返回接口返回的错误信息
  }
);
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App },
})
let bus = new Vue()
Vue.prototype.bus = bus



