import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Login from '@/components/login'
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      redirect: '/login',
      component: HelloWorld,
      meta: {requireAuth: false,},
    },
    {
      path: '/login',
      name: 'Login',

      component: Login,
      meta: {requireAuth: false,},
    }
  ]
});
export const powerRouter = [
  {
    path: '/list',
    meta: {requireAuth: true,},
    name: "list",
    redirect: '/list/A',
    hidden: false,
    component: resolve => require(["../components/list.vue"], resolve),
    children: [
      {
        path: '/list/A',
        name: 'A',
        component: resolve => require(["../components/A.vue"], resolve),
      },
      {
        path: '/list/B',
        name: 'B',
        component: resolve => require(["../components/B.vue"], resolve),
        meta: {role: 'B'}
      },
      {
        path: '/list/C',
        name: 'C',
        component: resolve => require(["../components/C.vue"], resolve),
        meta: {role: 'C'}
      },
      {
        path: '/list/D',
        name: 'D',
        component: resolve => require(["../components/D.vue"], resolve),
        // meta: {role: 'D'}//role未设置则共用
      },
    ],

  },
];



