<template>
  <div class="hello">
欢迎页
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data() {
    return {

    }
  },
  created() {
    let self = this;
    setTimeout(function(){
      alert('欢迎')
      self.$router.push({path: '/login', query: {page: 1, size: 10, box: 1}});
    },2000);

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



